// HEADER :: START

.header {
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;

  &:before {
    opacity: 0;
    z-index: 0;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include background-gradient(#00449D  , #001632, 'horizontal');
    @include transition(opacity);
  }

  &--fixed {
    &:before {
      opacity: 1;
      @include transition(opacity);
    }
  }

  .c-grid {
    max-width: 100%;
    @include respond(wide-small) {
      padding-left: 70px;
      padding-right: 70px;
    }
  }

  &__wrapper {
    z-index: 1;
    position: relative;
    display: flex;
    align-items: center;
    min-height: 75px;
    padding-top: 29px;
    @include transition(min-height, padding-top);
    @include respond(max-small) {
      padding-top: 0;
      min-height: 55px;
    }

    .header--fixed & {
      min-height: 65px;
      padding-top: 0;
      @include transition(min-height, padding-top);
      @include respond(max-small) {
        min-height: 55px;
      }
    }

    &-left {
      width: 211px;
      @include respond(max-medium) {
        width: 170px;
      }
      @include respond(max-small) {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
      }

      .hamburger {
        display: none;
        @include respond(max-small) {
          display: block;
        }
      }
    }

    &-right {
      display: flex;
      align-items: center;
      width: 100%;
      max-width: calc(100% - 211px);
      @include respond(max-medium) {
        max-width: calc(100% - 170px);
      }
      @include respond(max-small) {
        display: none;
        z-index: 100;
        overflow-y: auto;
        position: fixed;
        top: 55px;
        left: 0;
        width: 100%;
        max-width: 100%;
        height: calc(100vh - 55px);
        padding: 50px;
        background-color: rgba($c-white, 0.975);

        &.is-open {
          display: block;
          animation-fill-mode: both;
          animation-duration: 0.65s;
          animation-name: c-zoomIn;
        }
      }

      > div {
        display: flex;
        align-items: center;
        @include respond(max-small) {
          flex-direction: column;
        }

        &:first-of-type {
          width: 100%;
          max-width: calc(100% - 211px);
          @include respond(max-medium) {
            max-width: calc(100% - 170px);
          }
          @include respond(max-small) {
            max-width: 100%;
          }
        }

        &:last-of-type {
          display: flex;
          align-items: center;
          justify-content: space-between;
          width: 211px;
          @include respond(max-medium) {
            width: 170px;
          }
          @include respond(max-small) {
            width: 100%;
          }
        }
      }
    }
  }

  &__link {
    display: flex;
    align-items: center;
    color: $c-white;
    font-weight: bold;
    font-family: 'Muli', sans-serif;
    @include respond(max-small) {
      justify-content: center;
      width: 100%;
      min-height: 50px;
      color: $c-black;
      border-bottom: 1px solid rgba($c-black, 0.025);
    }

    &:hover {
      svg {
        fill: rgba($c-white, 1);
        @include transition(fill);
      }
    }

    &-0  {}
    &-1  {
      border-bottom: none;
    }

    svg {
      fill: rgba($c-white, 0.4);
      margin-right: 12px;
      @include transition(fill);
      @include respond(max-small) {
        display: none;
      }
    }
  }
}

@keyframes c-zoomIn {
  from {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }

  50% {
    opacity: 1;
  }
}

.zoomIn {
  -webkit-animation-name: zoomIn;
  animation-name: zoomIn;
}

// HEADER :: END